<template lang='pug'>
    div
        el-card
            div.methods
                .methods-left
                    h3 用户信息列表
                    //- inputEdit(type='number', inputData='1324')
                .methods-right
                    el-input(style='width: 200px; margin-right: 10px;', size='small')
                    el-button(type='success', size='small') 搜索机主
                    el-button(type='primary', @click='addModel=true', size='small') 新增机主
                    el-dialog(title='新增用户', :visible.sync='addModel', width='30%', close-on-click-modal=false)
                        .content
                            el-form(ref='formAddUser', :model='formAddUser', :rules='ruleAddUser', label-width='80px', label-position='right')
                                el-form-item(label='手机', prop='phone')
                                    el-input(v-model='formAddUser.phone')
                                el-form-item(label='姓名', prop='name')
                                    el-input(v-model='formAddUser.name')
                                el-form-item(label='ID', prop='thisID')
                                    el-input(v-model='formAddUser.thisID')
                                el-form-item(label='地址', prop='address')
                                    el-input(v-model='formAddUser.address')
                                el-form-item(label='email', prop='email')
                                    el-input(v-model='formAddUser.email')
                        span.dialog-footer(slot='footer')
                            el-button(@click='addCancel("formAddUser")') 取消
                            el-button(type='primary', @click='addOk("formAddUser")') 确定
            .user-table
                .table-head
                    el-row(:gutter='10', style='width: 100%')
                        el-col(:span='2')
                            .table-head__item 机主姓名
                        el-col(:span='2')
                            .table-head__item 手机
                        //- el-col(:span='3')
                        //-     .table-head__item 身份证信息
                        el-col(:span='5')
                            .table-head__item 关联旅客
                        el-col(:span='3')
                            .table-head__item 通讯地址
                        el-col(:span='3')
                            .table-head__item 邮箱地址
                        el-col(:span='3')
                            .table-head__item 发票信息
                .table-body
                    .table-body__block(v-if='(typeof userData) !== "string"')
                        .host-line(v-for='(item, index) in userData')
                            el-row(:gutter='10', style='width: 100%')
                                el-col.host-line__element(:span='2')
                                    .host-line__item {{item.Name}}
                                el-col.host-line__element(:span='2')
                                    .host-line__item {{item.Phone}}
                                //- el-col.host-line__element(:span='3')
                                //-     .host-line__item {{item.thisID}}
                                el-col.host-line__element(:span='5')
                                    .host-line__item
                                        el-card.edit-card.friend(v-for='(item_fri, index_fri) in item.Friends', :key='index_fri', :body-style="{padding: '10px'}")
                                            .friend__name.friend__item
                                                span.friend__item_label 姓名：
                                                span {{item_fri.Name}}
                                            .friend__phone.friend__item
                                                span.friend__item_label 手机：
                                                span {{item_fri.Phone}}
                                            .friend__id.friend__item
                                                span.friend__item_label ID：
                                                span {{item_fri.thisID}}
                                            .edit-card__btn
                                                i.el-icon-edit-outline.icon_text(title='编辑', @click='friendEdit(item_fri, item.Phone)')
                                        .add-friend
                                            el-button.btn-add
                                                i.el-icon-plus
                                el-col.host-line__element(:span='3')
                                    .host-line__item
                                        .address(v-for='(item_addr, index_addr) in item.Address')
                                            span
                                                //- inputEdit(v-model='userData[index].Address[index_addr]')
                                                el-card.edit-card(:body-style="{padding: '10px'}") {{userData[index].Address[index_addr]}}
                                                    .edit-card__btn
                                                        i.el-icon-edit-outline.icon_text(title='编辑', @click='modelData.friendEdit=true')
                                        .add-address
                                            el-button.btn-add(@click='addAddress(item)')
                                                i.el-icon-plus
                                el-col.host-line__element(:span='3')
                                    .host-line__item
                                        .email(v-for='(item_email, index_email) in item.Email')
                                            span
                                                //- inputEdit(v-model='userData[index].Email[index_email]')
                                                el-card.edit-card(:body-style="{padding: '10px'}") {{userData[index].Email[index_email]}}
                                                    .edit-card__btn
                                                        i.el-icon-edit-outline.icon_text(title='编辑', @click='modelData.friendEdit=true')
                                        //- .add-email
                                        //-     el-button.btn-add
                                        //-         i.el-icon-plus
                                el-col.host-line__element(:span='3')
                                    .host-line__item
                                        el-card.edit-card.invoice(v-for='(item_invo, index_invo) in item.Invoice', :body-style="{padding: '10px'}", :key='index_invo')
                                            .invoice__title
                                                span {{item_invo.Title}}
                                            .invoice__num
                                                span {{item_invo.taxNo}}
                                            .edit-card__btn
                                                i.el-icon-edit-outline.icon_text(title='编辑', @click='friendEdit(item_fri, item.Phone)')
                                        .add-invoice
                                            el-button.btn-add
                                                i.el-icon-plus
            .user-model
                el-dialog(title='朋友编辑', :visible.sync='modelData.friendEdit', width='30%')
                    el-form(:model='formFriend', label-width='80px')
                        el-form-item(label='姓名')
                            el-input(v-model='formFriend.name')
                        el-form-item(label='电话')
                            el-input(v-model='formFriend.phone')
                        el-form-item(label='ID')
                            el-input(v-model='formFriend.thisID')
                    span.dialog-footer(slot='footer')
                        el-button(type='danger') 删除
                        el-button(type='primary', @click='friendChange') 保存
</template>

<script>
import inputEdit from '../../common/InputEdit'
export default {
    components: {
        inputEdit
    },
    data () {
        let validatePhone = (rule, value, callback) => {
            let reg = /^1[345789]\d{9}$/
            if (value === '') {
                callback(new Error('请输入电话号码'))
            } else if (reg.test(value)) {
                callback()
            } else {
                callback(new Error('电话号码格式错误'))
            }
        }
        let validateID = (rule, value, callback) => {
            let reg = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/
            if (value === '') {
                callback(new Error('身份证号不能为空'))
            } else if (reg.test(value)) {
                callback()
            } else {
                callback(new Error('身份证号码格式错误'))
            }
        }
        return {
            userData: [],
            addModel: false,
            modelData: {
                friendEdit: false,
                addressEdit: false,
                emailEdit: false,
                invoiceEdit: false
            },
            formAddUser: {
                phone: '',
                name: '',
                thisID: '',
                address: '',
                email: ''
            },
            ruleAddUser: {
                phone: [
                    // { required: true, message: '电话号码不能为空', trigger: 'blur' },
                    { validator: validatePhone, trigger: 'blur' },
                    { required: true }
                ],
                name: [
                    { required: true, message: '用户姓名不能为空', trigger: 'blur' }
                ],
                thisID: [
                    { validator: validateID, trigger: 'blur' },
                    { required: true }
                ],
                address: [
                    { required: false }
                ],
                email: [
                    { required: false },
                    {type: 'email', message: '邮箱格式不正确', trigger: 'blur'}
                ]
            },
            formFriend: {
                name: '',
                phone: '',
                thisID: '',
                pastPhone: ''
            }
        }
    },
    methods: {
        addAddress (item) {

        },
        addOk (name) {
            let _this = this
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$http.post('/user/add/', _this.formAddUser)
                        .then(function (res) {
                            console.log(res.data)
                            _this.addModel = false
                            _this.$message({
                                message: 'ojbk',
                                type: 'success'
                            })
                        }).catch(function (error) {
                            console.log(error)
                        })
                }
            })
        },
        addCancel (name) {
            this.addModel = false
            this.$refs[name].resetFields()
            this.$message('取消添加')
        },
        friendEdit (item) {
            this.modelData.friendEdit = true
            this.formFriend.pastPhone = item.Phone
            this.formFriend.name = item.Name
            this.formFriend.phone = item.Phone
            this.formFriend.thisID = item.thisID
        },
        friendChange () {
            // let _this = this
            this.$http.put('/friend/change/', this.formFriend)
                .then(function (res) {
                    console.log(res.data)
                }).catch(function (error) {
                    console.log(error)
                })
        }
    },
    created () {
        let _this = this
        this.$http.get('/all/show/')
            .then(function (res) {
                console.log(res.data)
                _this.userData = res.data
            }).catch(function (error) {
                console.log(error)
            })
    },
    mounted () {
    }
}
</script>

<style scoped>
    .methods {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #dfdfdf;
    }
    .methods-left {
        float: left;
    }
    .methods-left > h3 {
        font-size: 20px;
        font-weight: bold;
    }
    .methods-right {
        float: right;
    }
    .methods::after {
        content: '';
        display: block;
        clear: both;
    }

    .table-head, .host-line {
        display: flex;
        /* justify-content: space-between; */
    }
    .table-head {
        padding-bottom: 5px;
        margin-bottom: 15px;
        font-size: 18px;
        font-weight: bold;
        border-bottom: 1px solid black
    }
    .table-head__item {
        /* background-color: red; */
        text-align: center;
    }
    /* table-body */
    .host-line {
        padding: 10px 0;
        border-bottom: 1px solid #dfdfdf;
    }
    .host-line:hover {
        position: relative;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2)
    }
    .host-line__item {
        text-align: center;
        /* background-color: red; */
    }
    .host-line__element {
        min-height: 10px;
    }
    .friend {
        /* display: flex; */
        position: relative;
        /* margin-bottom: 5px; */
    }
    .friend__item {
        margin-bottom: 2px;
    }
    .friend__item_label {
        width: 50px;
        display: inline-block;
    }
    .friend > .friend__name {
        width: 60px;
    }
    .friend > .friend__phone {
        width: 100px;
    }
    .friend > .friend__id {
        width: 120px;
    }
    .btn-add {
        padding: 2px 10px;
    }
    .edit-card {
        cursor: pointer;
        margin-bottom: 5px;
        background: #fdfdfd;
        font-size: 15px;
        position: relative;
        padding-right: 20px;
        text-align: left;
    }
    .edit-card__btn > .icon_text {
        font-size: 20px;
        cursor: pointer;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
    }
    .edit-card__btn > .icon_text:hover {
        color: #2db7f5;
    }
</style>



